<template>
  <div class="app-select">
    <div class="app-select__input" ref="input_block">
      <slot name="custom-input" :current="currentItem">
        {{ currentItem }}
      </slot>

      <img
        class="app-select__input-arrow"
        src="@/assets/icons/arrow-down.svg"
        alt="arrow"
      />
    </div>

    <ul class="app-select__list" ref="list_block">
      <li
        v-for="item in list"
        class="app-select__list-item"
        :key="item.id"
        @click="setActive(item.id)"
      >
        <router-link class="app-select__item-link" v-if="isLink" :to="item.to">
          {{ item[itemKey] }}
        </router-link>

        <span v-else>
          {{ item[itemKey] }}
        </span>
      </li>
    </ul>
  </div>
</template>

<script setup>
import { defineProps, ref, onMounted } from "vue";

const props = defineProps({
  itemKey: {
    type: String,
    required: true
  },
  isLink: {
    type: Boolean,
    default: false
  },
  list: {
    type: Array,
    default: () => []
  }
});

const input_block = ref(null);
const list_block = ref(null);

const currentItem = ref(props.list[0] ? props.list[0][props.itemKey] : "");

function setActive(id) {
  const filteredData = props.list?.filter((item) => item.id === id)[0];
  currentItem.value =
    filteredData && filteredData[props.itemKey]
      ? filteredData[props.itemKey]
      : "";
}

onMounted(() => {
  if (!props.isLink) {
    input_block.value.style.minWidth = list_block.value?.clientWidth + "px";
  }
});
</script>

<style lang="less">
.app-select {
  position: relative;

  padding: 0 5px;

  line-height: 1.15;
  color: @green;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.1em;

  text-transform: uppercase;

  &:hover {
    .app-select__list {
      height: auto;
      padding-top: 5px;

      opacity: 1;
      pointer-events: auto;
      transition: opacity 0.3s;
    }

    .app-select__input-arrow {
      transform: rotateZ(180deg);
    }
  }

  &__input {
    display: flex;
    align-items: center;
    gap: 7px;

    cursor: pointer;
  }

  &__input-arrow {
    transform: rotateZ(0);
    transition: transform 0.2s;
  }

  &__list {
    display: flex;
    flex-direction: column;

    position: absolute;
    top: 100%;
    left: 0;

    height: 0;
    min-width: 100%;
    padding: 0;
    margin: 0;

    opacity: 0;
    background-color: @white;
    overflow: hidden;
    pointer-events: none;
    z-index: 10;

    transition: opacity 0.3s;
  }

  &__list-item {
    cursor: pointer;

    padding: 4px 5px;
    line-height: 1.5;

    &:hover {
      @media (hover: hover) {
        background-color: fade(@green, 30%);
      }
    }

    &:active {
      background-color: fade(@green, 30%);
    }
  }

  &__item-link {
    text-decoration: none;
    color: inherit;
  }
}
</style>
