<template>
  <header class="app-header">
    <div class="app-header__container">
      <router-link class="app-header__logo" to="/">
        <img
          class="app-header__logo-icon"
          src="@/assets/icons/logo.svg"
          alt="logo"
        />
      </router-link>

      <AppSelect
        class="app-header__cities"
        :list="citiesSorted"
        :item-key="'city'"
      />

      <nav class="app-header__nav">
        <AppSelect
          v-for="(value, key) in navData"
          :list="value"
          :item-key="'name'"
          :key="key"
          :is-link="true"
        >
          <template v-slot:custom-input="{ current }">
            {{ current && key }}
          </template>
        </AppSelect>
      </nav>

      <div class="app-header__info">
        <a class="app-header__info-phone" href="tel:+73412918742">
          + 7 3412 918-742
        </a>

        <div class="app-header__info-graph">с 9:00 до 19:00</div>
      </div>

      <div class="app-header__user">
        <img
          class="app-header__user-icon"
          src="@/assets/icons/profile.svg"
          alt="profile"
        />

        <router-link class="app-header__user-basket" to="/basket">
          КОРЗИНА
        </router-link>
      </div>

      <button
        type="button"
        class="app-header__burger"
        :class="{ 'app-header__burger--active': showMenu }"
        @click="setShowMenu"
      >
        <div class="app-header__burger-line"></div>
      </button>

      <aside
        class="app-header__mobile-menu"
        :class="{ 'app-header__mobile-menu--active': showMenu }"
      >
        <div
          class="app-header__menu-block"
          v-for="(nav, title) in navData"
          :key="title"
        >
          <h6 class="app-header__menu-title">{{ title }}</h6>

          <ul class="app-header__menu-list">
            <li
              class="app-header__menu-item"
              v-for="item in nav"
              :key="item.id"
            >
              <router-link class="app-header__item-link" :to="item.to">
                {{ item.name }}
              </router-link>
            </li>
          </ul>
        </div>
      </aside>
    </div>
  </header>
</template>

<script setup>
import AppSelect from "@/components/AppSelect";
import { ref, computed } from "vue";

import citiesData from "@/data/cities.json";
import navData from "@/data/nav.json";

const cities = ref(citiesData);
const showMenu = ref(false);

const citiesSorted = computed(() => {
  return cities.value && Array.isArray(cities.value)
    ? Array.from(cities.value).sort((cur, prev) => {
        if (cur.city > prev.city) return 1;
        if (cur.city < prev.city) return -1;
        return 0;
      })
    : [];
});

function setShowMenu() {
  showMenu.value = !showMenu.value;
}
</script>

<style lang="less">
.app-header {
  box-sizing: border-box;

  display: flex;
  align-items: center;

  max-height: 70px;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: @white;

  @media @bw768 {
    max-height: 110px;
  }

  @media @bw400 {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__container {
    .container();

    display: flex;
    align-items: center;

    @media @bw768 {
      flex-wrap: wrap;
      gap: 15px 0;
    }
  }

  &__logo {
    margin-right: 25px;

    @media @bw768 {
      margin-right: 15px;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    gap: 0 50px;

    margin-left: auto;

    @media @bw1020 {
      gap: 0 8px;
    }

    @media @bw400 {
      display: none;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 7px 0;

    margin-left: auto;

    @media @bw768 {
      width: 70%;
      margin-left: 0;
    }

    @media @bw400 {
      order: 3;
    }
  }

  &__info-phone {
    line-height: 1.15;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0.1em;
    color: @green;
    text-decoration: none;
  }

  &__info-graph {
    line-height: 1;
    font-size: 10px;
    font-weight: 500;
    color: @gray;
  }

  &__user {
    margin-left: 125px;

    line-height: 1.15;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0.1em;
    color: @green;

    @media @bw1020 {
      align-self: flex-start;
      margin-left: auto;
    }

    @media @bw400 {
      align-self: center;
      order: 2;

      margin-right: 20px;
    }
  }

  &__user-icon {
    margin-right: 10px;
  }

  &__user-basket {
    text-decoration: none;
    color: @green;
  }

  &__burger {
    display: none;
    align-items: center;

    position: fixed;
    top: 10px;
    right: 10px;

    z-index: 1000;

    width: 20px;
    height: 20px;
    padding: 5px;
    margin-left: 5px;

    background-color: transparent;
    border: none;

    &--active {
      & .app-header__burger-line {
        background-color: transparent;
        &::before {
          top: 0;
          transform: rotateZ(45deg);
        }

        &::after {
          top: 0;
          transform: rotateZ(-45deg);
        }
      }
    }

    @media @bw400 {
      display: flex;
    }
  }

  &__burger-line {
    position: relative;

    width: 100%;
    height: 2px;

    background-color: @gray;

    &::before {
      content: "";
      display: block;

      position: absolute;
      top: -4px;
      left: 0;

      width: 100%;
      height: 2px;
      margin-bottom: 5px;

      background-color: @gray;
      transform: rotateZ(0);

      transition: transform 0.2s;
    }

    &::after {
      content: "";
      display: block;

      position: absolute;
      top: 4px;
      left: 0;

      width: 100%;
      height: 2px;
      margin-bottom: 5px;

      background-color: @gray;
      transform: rotateZ(0);

      transition: transform 0.2s;
    }
  }

  &__mobile-menu {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    padding: 10px;

    background-color: @white;

    transform: translateX(100%);

    transition: transform 0.3s;

    &--active {
      transform: translateX(0);
    }
  }

  &__menu-title {
    margin: 0;

    color: @gray;
    font-size: 16px;
    font-weight: 500;

    &:first-letter {
      text-transform: uppercase;
    }
  }

  &__menu-list {
    list-style: none;
    padding-left: 5px;
    margin: 10px 0 15px;
  }

  &__menu-item:not(:last-child) {
    margin-bottom: 5px;
  }

  &__item-link {
    text-decoration: none;
    color: @green;

    transition: color 0.2s;

    &:hover {
      @media (hover: hover) {
        color: darken(@green, 10%);
      }
    }

    &:active {
      color: darken(@green, 10%);
    }
  }
}
</style>
